	module.exports = {"data":{"markdownRemark":{"html":"<p>某ミニゲーム薪割りのトレーナーを制作、が、全く別のゲームへと進化(少しずつ) 次々と薪やウサギが出てきますが、ウサギを斬らずに薪だけを割るというシンプルながら非常に中毒性の高いゲームです。薪が出たのに割らなかったり、ウサギを割ったりすると、即ゲームオーバーです。(つまり某薪割りと基本ルールは同じですね^^;) 時間制限もシビアですので、研ぎ澄まされた反射神経も必要となってきます。のんびりしていられません。 β1及びβ3ではプレイヤー視点、β2.1では旧FFのような横視点となっています。</p>\n<ul>\n<li>ジャンル: ワンキーアクション</li>\n<li>開発言語(旧): Microsoft QBasic 1.1</li>\n<li>開発言語(移植版): HSP 2.4</li>\n<li>最終更新: 2000/2/1</li>\n<li><a href=\"/filez/app/makiwari.zip\">Version β3をダウンロード</a> ([filesize]<a href=\"https://danmaq.com/filez/app/makiwari.zip%5B/filesize%5D\">https://danmaq.com/filez/app/makiwari.zip[/filesize]</a>)</li>\n<li><a href=\"/filez/app/mk_old.zip\">Version β2.1をダウンロード</a> ([filesize]<a href=\"https://danmaq.com/filez/app/mk_old.zip%5B/filesize%5D\">https://danmaq.com/filez/app/mk_old.zip[/filesize]</a>)</li>\n<li><a href=\"/filez/app/mk_b1.zip\">Version β1をダウンロード</a> ([filesize]<a href=\"https://danmaq.com/filez/app/mk_b1.zip%5B/filesize%5D\">https://danmaq.com/filez/app/mk_b1.zip[/filesize]</a>)</li>\n</ul>","frontmatter":{"cover":null,"date":"1999-07-21T00:00:00+09:00","strDate":"1999/7/20","title":"Mk-IIK(旧称:薪割り2K)"}}},"pathContext":{"path":"/ja/blog/1999/07/21-mk-iik/","langKey":"ja"}}

